.wrapper {
  max-width: 1280px;
  margin: auto;
  
  display: grid;

  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr;
  grid-gap: 10px;

  grid-template-areas: 
  "header header"
  "nav main";
}

.wrapper__main {
  grid-area: main;
}

.logo {
  height: 100%;
}





