.general {
    margin: 10px;
    width: fit-content;
    border-radius: 3px;
    position: relative;
    padding: 5px 25px 5px 10px;
    align-items: center;
    background-color: rgb(235, 235, 235);
}


.email {
    font-size: 21px;
    color: var(--color-black);
}

.control {
    display: flex;
    opacity: 0;
    transition: all .3s ease-in;
}

.controlEdit {
    opacity: 1;
}

.general:hover .control {
    opacity: 1;
}


.delete {
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
    opacity: .8;
    color: black;
    font-weight: bold;
    transition: all .2s linear;
}

.delete:hover {
    opacity: 1;
}
