.general {
    font-style: normal;
    color: var(--color-black);
}

.form {
    width: 100%;
    margin-top: 30px;
    padding: 10px 20px 20px;
    box-sizing: border-box;
    display: flex;
}

.inputPlace {
    margin-right: 20px;
    padding: 7px 10px;
    width: 75%;
    border: none;
    outline: none;
    border-bottom: 1px solid black;
}

.inputPlace::placeholder {
    font-size: 16px;
    line-height: 20px;
    color: #bcbcc4;
}

.button {
    width: 25%;
    height: 40px;
    font-weight: 600;
    font-size: 17px;
    line-height: 17px;
    box-sizing: border-box;
    border: 1px solid black;
    border-radius: 5px;
    background-color: transparent;
    transition: all 0.3s ease;
    outline: none;
    user-select: none;
    cursor: pointer;
}

.button:disabled {
    cursor: default;
    border: 1px solid #999999;
}

.commonError {
    color: red;
    background-color: aliceblue;
}