.general {
    width: 80%;
    margin: auto;
}



.button {
    width: 25%;
    height: 40px;
    font-weight: 600;
    font-size: 17px;
    line-height: 17px;
    box-sizing: border-box;
    border: 1px solid black;
    border-radius: 5px;
    background-color: transparent;
    transition: all 0.3s ease;
    outline: none;
    user-select: none;
    cursor: pointer;

    display: block;
    margin: auto;
}

.button:disabled {
    cursor: default;
    border: 1px solid #999999;
}